export default {
    data() {
        return {
            credit: '--',
            incAmount: '--',
            rank: '--',
            beginAt: undefined
        }
    },
    mounted() {
        this.$http({
                url: `${BASE_URL}/lock-account/monthly_credit_log`,
        }).then((result) => {
            const {status, data, credit } = result.data;
            const record = data[0];
            if (status == 0) {
                this.credit = credit;
                this.rank = record.rank;
                this.incAmount = record.inc_amount;
                this.beginAt = record.begin_at;
            }
        });
    }
}



